    <div class="left">
      <nav>
        <p class="uppercase font-bold pb-6">
          版本 <span class="cur">1.18.0</span></p>
        <h3>
          介绍
        </h3>
        <ul>
          <li>
            <ul>
              <li :class="type=='nohost-btn'?'cur':''">
                <a @click="changeType('nohost-btn')">
                  小圆点功能介绍
                </a>
              </li>
              <li :class="type=='changelog'?'cur':''">
                <a @click="changeType('changelog')">
                  版本更新
