      </nav>
    </div>
    <div class="right">
      <mavon-editor :boxShadow="false"
                    defaultOpen="preview"
                    :subfield="false"
                    class='editor'
                    :toolbarsFlag="false"
                    v-model="txt" />
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      type: "nohost-btn"
    }
  },
  created () {
    let _this = this;
    let typeArr = ['nohost-btn', 'changelog ']
    this.type = typeArr[0];
    this.requestMd();
  },
  computed: {
    txt: {
      get: function () {
        return this.$store.state.txt;
      },
      set: function () { }
    }
  },
  methods: {
    requestMd () {
      let _this = this;
      this.$http({
        method: "GET",
        url: `https://nohost-1300076408.cos.ap-guangzhou.myqcloud.com/${this.type}.md`
      }).then(res => {
        let txt = (res && res.data) || "请检查文件是否存在";
        _this.$store.commit("setTxt", txt);
      });
    },
    changeType (type) {
      this.type = type;
      this.requestMd();
    }
  }
}
</script>

